.rhap_progress-section {
  display: flex;
  height: 100%;
  flex: 3 1 auto;
  align-items: center;
  flex-direction:row;
  justify-content:center;
}

.rhap_progress-container {
  display: flex;
  align-items: center;
  height: 20px;
  flex: 1 0 100px;
  align-self: center;
  margin: 0 calc(10px + 1%);
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
}
.rhap_progress-container:focus:not(:focus-visible) {
  outline: 0;
}



.rhap_progress-bar {
  box-sizing: border-box;
  position: relative;
  z-index: 0;
  width: 100%;
  height: 4px;
  background-color: #dddddd;
  border-radius: 2px;
}

.rhap_progress-filled {
  height: 100%;
  position: absolute;
  z-index: 2;
  background-color: #000000;
  border-radius: 2px;
}

.rhap_progress-indicator {
  box-sizing: border-box;
  position: absolute;
  z-index: 3;
  width: 10px;
  height: 10px;
  
  margin-left: -5px;
  top: -3px;
  
  background: #000000;
  border-radius: 50px;
/*   box-shadow: rgba(134, 134, 134, 0.5) 0 0 5px; */
}
.rhap_time {
  color: #333;
  /* font-size: 16px; */
  font-size: 1.4rem;
  font-weight: 400;
  user-select: none;
  -webkit-user-select: none;
}



