.rhap_controls-section {
  width:100%;
  display: flex;
  flex: 0 0 auto;
  justify-content: flex-end;
  align-items: center;
}

.rhap_volume-controls {
  display: flex;
  width:100%;
  height:100%;
  flex: 0 0 auto;
  justify-content: flex-end;
  align-items: center;
}

.rhap_volume-button {
/*   flex: 0 0 26px;
