  flex:1 0 40px;

}

.rhap_volume-container {
  display: flex;
  width:100%;
  align-items: center;
  flex: 0 1 100px;
  user-select: none;
  -webkit-user-select: none;
}

.rhap_volume-bar-area {
  display: flex;
  align-items: center;
  width: 100%;
  height: 14px;
  cursor: pointer;
}
.rhap_volume-bar-area:focus:not(:focus-visible) {
  outline: 0;
}

.rhap_volume-bar {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  height: 4px;
  background: #dddddd;
  border-radius: 2px;
}

.rhap_volume-indicator {
  box-sizing: border-box;
  position: absolute;
  width: 10px;
  height: 10px;
  margin-left: -4px;
  left: 0;
  top: -3px;
  background: #000;
  opacity: 0.9;
  border-radius: 50px;
/*   box-shadow: rgba(134, 134, 134, 0.5) 0 0 3px; */
  cursor: pointer;
}
.rhap_volume-indicator:hover {
  opacity: 0.9;
}

.rhap_volume-filled {
  height: 100%;
  position: absolute;
  z-index: 99;
  background-color: #000000;
  border-radius: 2px;
}

/* Utils */
.rhap_button-clear {
  background-color: transparent;
  border: none;
  padding: 0;
  margin: 0;
  line-height: 0;
  /* height: 100%; */
  /* align-content: center; */
  
  /* overflow: hidden; */
/*   cursor: pointer; */
}
.rhap_button-clear:hover {
  opacity: 0.9;
  transition-duration: 0.2s;
}
.rhap_button-clear:active {
  opacity: 0.95;
}
.rhap_button-clear:focus:not(:focus-visible) {
  outline: 0;
}
